export default class Cards{
    constructor(){
        this.cards = [];
    }

    /**
     *
     * @param {Array} cards массив карточек
     * @return {*} возвращает ошибку, если появилась
     */
    init(cards){
        try{
            this.cards = cards;
        } catch (err){
            return err;
        }
    }
}